import React from "react";
import ProjectCards from "../components/ProjectCards"
import Projects from "../assets/projects.json";
import Carousel from "react-bootstrap/Carousel"

function Container() {
    return (
        <Carousel>
            {
                Projects.map((x, i) =>
                    <ProjectCards
                        key={i + '-img'}
                        title={x.title}
                        url={x.url}
                        git={x.gitHub}
                        img={x.img}
                        desc={x.description}
                    />
                )
            }
        </Carousel>
    )
}

export default Container;